'use client';

import type { FC } from 'react';
import { useState } from 'react';
import { Wand2 } from 'lucide-react';
import { getAdviceAction } from '@/lib/actions';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { cn } from '@/lib/utils';
import type { SpendingRecommendationsOutput } from '@/ai/flows/spending-recommendations';

interface AiAdviceProps {
  income: number;
  savingsGoal: number;
  spentToday: number;
}

const AiAdvice: FC<AiAdviceProps> = ({ income, savingsGoal, spentToday }) => {
  const [advice, setAdvice] = useState<SpendingRecommendationsOutput | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleGetAdvice = async () => {
    setIsLoading(true);
    setError(null);
    setAdvice(null);
    try {
      const result = await getAdviceAction({
        income,
        savingsGoal,
        expenses: spentToday,
      });
      setAdvice(result);
    } catch (e) {
      setError('Failed to get advice. Please try again.');
      console.error(e);
    }
    setIsLoading(false);
  };

  return (
    <Card className="bg-card/80 backdrop-blur-sm">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Wand2 className="text-accent" />
          <span>AI Spending Coach</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="flex flex-col gap-4 items-start">
        {isLoading ? (
          <div className="space-y-2 w-full">
            <Skeleton className="h-6 w-1/4" />
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-4 w-3/4" />
          </div>
        ) : advice ? (
          <div className="space-y-2">
            <p
              className={cn(
                'font-bold text-xl',
                advice.status === 'Over Budget'
                  ? 'text-destructive'
                  : 'text-primary'
              )}
            >
              {advice.status}
            </p>
            <p className="text-muted-foreground">{advice.advice}</p>
          </div>
        ) : error ? (
            <p className="text-destructive">{error}</p>
        ) : (
          <p className="text-muted-foreground">
            Get personalized advice to help you meet your savings goal.
          </p>
        )}
        <Button onClick={handleGetAdvice} disabled={isLoading} variant="secondary">
          {isLoading ? 'Thinking...' : 'Get Advice'}
        </Button>
      </CardContent>
    </Card>
  );
};

export default AiAdvice;
