'use client';

import { useCallback, useEffect, useState } from 'react';
import { useLocalStorage } from '@/hooks/use-local-storage';
import { useToast } from '@/hooks/use-toast';
import type { Expense } from '@/lib/types';
import AiAdvice from './AiAdvice';
import ExpenseTracker from './ExpenseTracker';
import SettingsForm from './SettingsForm';
import SummaryCards from './SummaryCards';
import { Skeleton } from '@/components/ui/skeleton';

const SpendSmartDashboard = () => {
  const [isClient, setIsClient] = useState(false);
  const [income, setIncome] = useLocalStorage('income', 3000);
  const [savingsGoal, setSavingsGoal] = useLocalStorage('savingsGoal', 500);
  const [expenses, setExpenses] = useLocalStorage<Expense[]>('expenses', []);
  const { toast } = useToast();

  useEffect(() => {
    setIsClient(true);
  }, []);

  const handleAddExpense = (newExpense: { description: string; amount: number }) => {
    const expenseToAdd: Expense = {
      id: new Date().getTime().toString(),
      date: new Date().toISOString().split('T')[0],
      ...newExpense,
    };
    setExpenses((prevExpenses) => [...prevExpenses, expenseToAdd]);
    toast({
      title: 'Expense Added',
      description: `${newExpense.description} for $${newExpense.amount} has been logged.`,
    });
  };

  const handleRemoveExpense = (expenseId: string) => {
    const removedExpense = expenses.find(exp => exp.id === expenseId);
    setExpenses((prevExpenses) => prevExpenses.filter((expense) => expense.id !== expenseId));
    if (removedExpense) {
      toast({
        variant: 'destructive',
        title: 'Expense Removed',
        description: `${removedExpense.description} has been removed.`,
      });
    }
  };
  
  const handleUpdateSettings = ({ income, savingsGoal }: { income: number; savingsGoal: number }) => {
    setIncome(income);
    setSavingsGoal(savingsGoal);
    toast({
      title: 'Settings Saved',
      description: 'Your income and savings goal have been updated.',
    });
  };

  if (!isClient) {
    return (
      <div className="flex flex-col gap-6 lg:gap-8">
        <div className="grid gap-4 md:grid-cols-3">
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
        </div>
        <Skeleton className="h-48" />
        <div className="grid md:grid-cols-2 gap-6 lg:gap-8">
          <Skeleton className="h-80" />
          <Skeleton className="h-80" />
        </div>
      </div>
    );
  }

  const today = new Date().toISOString().split('T')[0];
  const todayExpenses = expenses.filter((exp) => exp.date === today);
  const spentToday = todayExpenses.reduce((acc, exp) => acc + exp.amount, 0);

  const dailyBudget = (income - savingsGoal) / 30;
  const remainingToday = dailyBudget - spentToday;

  return (
    <div className="flex flex-col gap-6 lg:gap-8">
      <SummaryCards
        dailyBudget={dailyBudget > 0 ? dailyBudget : 0}
        spentToday={spentToday}
        remainingToday={remainingToday > 0 ? remainingToday : 0}
      />

      <AiAdvice income={income} savingsGoal={savingsGoal} spentToday={spentToday} />

      <div className="grid md:grid-cols-2 gap-6 lg:gap-8">
        <SettingsForm
          income={income}
          savingsGoal={savingsGoal}
          onUpdate={handleUpdateSettings}
        />
        <ExpenseTracker
          expenses={todayExpenses}
          onAddExpense={handleAddExpense}
          onRemoveExpense={handleRemoveExpense}
        />
      </div>
    </div>
  );
};

export default SpendSmartDashboard;
