'use client';

import type { FC } from 'react';
import { PiggyBank, TrendingDown, Wallet } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { formatCurrency } from '@/lib/utils';
import { cn } from '@/lib/utils';

interface SummaryCardsProps {
  dailyBudget: number;
  spentToday: number;
  remainingToday: number;
}

const SummaryCards: FC<SummaryCardsProps> = ({
  dailyBudget,
  spentToday,
  remainingToday,
}) => {
  return (
    <div className="grid gap-4 md:grid-cols-3">
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">Daily Budget</CardTitle>
          <PiggyBank className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold font-mono">
            {formatCurrency(dailyBudget)}
          </div>
          <p className="text-xs text-muted-foreground">
            Your spending limit for today.
          </p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">Spent Today</CardTitle>
          <TrendingDown className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold font-mono">
            {formatCurrency(spentToday)}
          </div>
          <p className="text-xs text-muted-foreground">
            Total expenses logged today.
          </p>
        </CardContent>
      </Card>
      <Card
        className={cn(
          'border-accent/50 bg-accent/10',
          remainingToday < dailyBudget * 0.2 && 'border-destructive/50 bg-destructive/10'
        )}
      >
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium text-accent-foreground">Remaining</CardTitle>
          <Wallet className="h-4 w-4 text-accent" />
        </CardHeader>
        <CardContent>
          <div className={cn('text-2xl font-bold font-mono text-accent-foreground', remainingToday < dailyBudget * 0.2 && 'text-destructive')}>
            {formatCurrency(remainingToday)}
          </div>
          <p className="text-xs text-accent-foreground/80">
            {remainingToday > 0 ? 'Funds left for today.' : 'You are over budget.'}
          </p>
        </CardContent>
      </Card>
    </div>
  );
};

export default SummaryCards;
