'use client';

import type { FC } from 'react';
import { zodResolver } from '@hookform/resolvers/zod';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { Landmark, PiggyBank, Save } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';

const settingsFormSchema = z.object({
  income: z.coerce.number().positive('Income must be a positive number.'),
  savingsGoal: z.coerce.number().min(0, 'Savings goal cannot be negative.'),
});

type SettingsFormValues = z.infer<typeof settingsFormSchema>;

interface SettingsFormProps {
  income: number;
  savingsGoal: number;
  onUpdate: (values: SettingsFormValues) => void;
}

const SettingsForm: FC<SettingsFormProps> = ({ income, savingsGoal, onUpdate }) => {
  const form = useForm<SettingsFormValues>({
    resolver: zodResolver(settingsFormSchema),
    defaultValues: {
      income,
      savingsGoal,
    },
  });
  
  const onSubmit = (data: SettingsFormValues) => {
    onUpdate(data);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Financial Settings</CardTitle>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="income"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="flex items-center gap-2">
                    <Landmark className="text-primary"/> Monthly Income
                  </FormLabel>
                  <FormControl>
                    <Input type="number" step="100" placeholder="e.g. 3000" {...field} />
                  </FormControl>
                  <FormDescription>Your total income for a 30-day period.</FormDescription>
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="savingsGoal"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="flex items-center gap-2">
                    <PiggyBank className="text-primary"/> Monthly Savings Goal
                  </FormLabel>
                  <FormControl>
                    <Input type="number" step="50" placeholder="e.g. 500" {...field} />
                  </FormControl>
                  <FormDescription>How much you want to save this period.</FormDescription>
                </FormItem>
              )}
            />
            <Button type="submit" disabled={!form.formState.isDirty}>
                <Save className="mr-2" />
                Save Settings
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
};

export default SettingsForm;
