'use server';

import {
  getSpendingRecommendations,
  type SpendingRecommendationsInput,
} from '@/ai/flows/spending-recommendations';

export async function getAdviceAction(input: SpendingRecommendationsInput) {
  try {
    const recommendations = await getSpendingRecommendations(input);
    return recommendations;
  } catch (error) {
    console.error('Error getting spending recommendations:', error);
    throw new Error('Could not retrieve spending advice.');
  }
}
